'use strict';

/*
* When adding modules to the namespace, ensure that they are added in alphabetical order according to module name.
*/

// MODULES //

import arcsine from '@stdlib/stats-base-dists-arcsine-cdf';
import beta from '@stdlib/stats-base-dists-beta-cdf';
import betaprime from '@stdlib/stats-base-dists-betaprime-cdf';
import cauchy from '@stdlib/stats-base-dists-cauchy-cdf';
import chi from '@stdlib/stats-base-dists-chi-cdf';
import chisquare from '@stdlib/stats-base-dists-chisquare-cdf';
import cosine from '@stdlib/stats-base-dists-cosine-cdf';
import erlang from '@stdlib/stats-base-dists-erlang-cdf';
import exponential from '@stdlib/stats-base-dists-exponential-cdf';
import f from '@stdlib/stats-base-dists-f-cdf';
import frechet from '@stdlib/stats-base-dists-frechet-cdf';
import gamma from '@stdlib/stats-base-dists-gamma-cdf';
import gumbel from '@stdlib/stats-base-dists-gumbel-cdf';
import invgamma from '@stdlib/stats-base-dists-invgamma-cdf';
import kumaraswamy from '@stdlib/stats-base-dists-kumaraswamy-cdf';
import laplace from '@stdlib/stats-base-dists-laplace-cdf';
import levy from '@stdlib/stats-base-dists-levy-cdf';
import logistic from '@stdlib/stats-base-dists-logistic-cdf';
import lognormal from '@stdlib/stats-base-dists-lognormal-cdf';
import normal from '@stdlib/stats-base-dists-normal-cdf';
import pareto1 from '@stdlib/stats-base-dists-pareto-type1-cdf';
import rayleigh from '@stdlib/stats-base-dists-rayleigh-cdf';
import t from '@stdlib/stats-base-dists-t-cdf';
import triangular from '@stdlib/stats-base-dists-triangular-cdf';
import uniform from '@stdlib/stats-base-dists-uniform-cdf';
import weibull from '@stdlib/stats-base-dists-weibull-cdf';


// MAIN //

var CDF = {};

CDF[ 'arcsine' ] = arcsine;
CDF[ 'beta' ] = beta;
CDF[ 'betaprime' ] = betaprime;
CDF[ 'cauchy' ] = cauchy;
CDF[ 'chi' ] = chi;
CDF[ 'chisquare' ] = chisquare;
CDF[ 'cosine' ] = cosine;
CDF[ 'erlang' ] = erlang;
CDF[ 'exponential' ] = exponential;
CDF[ 'f' ] = f;
CDF[ 'frechet' ] = frechet;
CDF[ 'gamma' ] = gamma;
CDF[ 'gumbel' ] = gumbel;
CDF[ 'invgamma' ] = invgamma;
CDF[ 'kumaraswamy' ] = kumaraswamy;
CDF[ 'laplace' ] = laplace;
CDF[ 'levy' ] = levy;
CDF[ 'logistic' ] = logistic;
CDF[ 'lognormal' ] = lognormal;
CDF[ 'normal' ] = normal;
CDF[ 'pareto-type1' ] = pareto1;
CDF[ 'rayleigh' ] = rayleigh;
CDF[ 't' ] = t;
CDF[ 'triangular' ] = triangular;
CDF[ 'uniform' ] = uniform;
CDF[ 'weibull' ] = weibull;


// EXPORTS //

export default CDF;
